    do projeto de componentes, utilize-o apenas como referência.
-->
<template>
    <div class="full-page">
        <x-authenticator v-model="options"></x-authenticator>
    </div>
</template>

<script>
import services from './services';

export default {
    name: 'AuthSample',
    data() {
        return {
            options: {
                info: {
                    version: null,
                },
                login: {
                    onEnter: this.login,
                    onLogout: this.logout,
                },
                forgot: {
                    user: {
                        firstName: null
                    },
                    onSendMail: this.sendMail,
                    onValidation: this.validation,
                    onResetPwd: this.resetPwd,
                    onCompleted: this.completed,
                },
            },
        }
    },
    methods: {

        async login(evt) {
            try {
                evt.loading();
                await this.$sleep(500);

                const user = await services.login(evt.fields);
                this.$store.commit('sample/SET_USER', user);

                this.$router.push({ name: 'sample_home' });
            } catch (err) {
                this.$alert(err.message);
            } finally {
                evt.loading(false);
            }
        },

        async logout(evt) {
            let res;
            try {
                evt.loading();
                services.logout();
                this.$store.commit('sample/SET_USER', {});
            } catch (err) { } finally {
                evt.loading(false);
            }
        },

        async sendMail(evt) {
            evt.loading();
            await this.$sleep(500);
            evt.loading(false);
            evt.next();
            console.log(evt);
        },

        async validation(evt) {
            evt.loading();
            await this.$sleep(500);
            this.options.forgot.user.firstName = 'MASTER';
            evt.loading(false);
            evt.next();
            console.log(evt);
        },

        async resetPwd(evt) {
            if (evt.fields.newPwd !== evt.fields.confirmPwd) {
                return this.$notify('Tudo indica que as senhas estão diferentes, por favor, verifique sua digitação.');
            }
            evt.loading();
            await this.$sleep(500);
            evt.loading(false);
            evt.next();
            console.log(evt);
        },

        async completed(evt) {
            evt.setStep(0);
            console.log(evt);
        },

        async loadInfo() {
            try {
                const res = await services.info();
                this.$store.commit('sample/SET_INFO', res.data);
                this.options.info = this.$store.state['sample'].info;
            } catch (err) {
                alert('Não foi possível obter informações do sistema! Msg: ' + err.message);
            }
        },
    },
    created() {
        this.loadInfo();
    },
}
</script>

